.reflection-wrapper {
     width: 100%; /* Adjust the width based on your needs */
  max-width: 500px; /* Set a maximum width for larger screens */
  margin: 0 auto; /* Center the boxes horizontally */
  padding: 0 20px; 
    
  
}

.title2 {
    font-weight: bold;
    color: grey;
    font-size: 20px;
    margin-bottom: 40px;
    
}

.question {
    text-align: left;
}

.input {
    margin-bottom: 25px;
    margin-top: 15px;
    width: 500px;
    height: 100px;
    font-family: Arial, Helvetica, sans-serif;
    border-color: grey;
    border-radius: 20px;
    padding: 10px;
   
}

@media only screen and (max-width: 600px) {

    .reflection-wrapper {
     width: 100%; /* Adjust the width based on your needs */
  max-width: 400px; /* Set a maximum width for larger screens */
  margin: 0 auto; /* Center the boxes horizontally */
  padding: 0 20px; 
    
  
}

.input {
    width: 400px;
}
    
   
}

@media only screen and (max-width: 500px) {

    .reflection-wrapper {
     width: 100%; /* Adjust the width based on your needs */
  max-width: 300px; /* Set a maximum width for larger screens */
  margin: 0 auto; /* Center the boxes horizontally */
  padding: 0 0px; 
    
  
}

.input {
    width: 300px;
}
    
   
}
